import { RegisterComponent } from '@/common/register-component';
import { NylasSchedulerConfigConnector } from '@/connector/nylas-scheduler-config-connector';
import { debug } from '@/utils/utils';
import { AttachInternals, Component, Element, Event, EventEmitter, Host, Listen, Prop, State, Watch, h } from '@stencil/core';
import { NylasSchedulerEditor } from '../nylas-scheduler-editor/nylas-scheduler-editor';
import { Configuration } from '@nylas/core';

/**
 * The `nylas-custom-booking-flow` component is a UI component that displays the booking calendar picker.
 * @part ncbf - The custom booking flow container
 * @part ncbf__header - The header of the custom booking flow
 * @part ncbf__dropdown - The dropdown container
 * @part ncbf__dropdown-button - The dropdown button
 * @part ncbf__dropdown-content - The dropdown content
 */
@Component({
  tag: 'nylas-custom-booking-flow',
  styleUrl: 'nylas-custom-booking-flow.scss',
  shadow: true,
  formAssociated: true,
})
export class NylasCustomBookingFlow {
  @Element() host!: HTMLNylasCustomBookingFlowElement;
  /**
   * The selected config
   */
  @Prop() selectedConfiguration?: Configuration;

  /**
   * The name of the calendar picker.
   */
  @Prop() name: string = 'booking-type';
  /**
   * The booking type.
   */
  @Prop() bookingType: string = '';
  /**
   * The element internals.
   */
  @AttachInternals() internals!: ElementInternals;

  /**
   * The selected bookingType.
   */
  @State() selectedBookingType: string = '';

  /**
   * This event is fired when the selected bookingType changes.
   */
  @Event() valueChanged!: EventEmitter<{
    value: string;
    name: string;
    valueChanged?: (event: CustomEvent<{ value: string; name: string }>) => void;
  }>;

  /**
   * When a name prop is passed, stencil does not automatically set the name attribute on the host element.
   * Since this component is form-associated, the name attribute is required for form submission.
   * This is a workaround to ensure that the name attribute is set on the host element.
   */
  @Watch('name')
  elementNameChangedHandler(newValue: string) {
    debug('nylas-custom-booking-flow', 'elementNameChangedHandler', newValue);
    this.host.setAttribute('name', newValue);
  }

  @Watch('selectedConfiguration')
  selectedConfigurationChangedHandler(newValue: Configuration, oldValue: Configuration) {
    debug('nylas-custom-booking-flow', 'selectedConfigurationChangedHandler', newValue, oldValue);
    if (newValue?.event_booking?.booking_type) {
      this.bookingType = newValue?.event_booking?.booking_type;
    }
    // TODO: Remove this when the internals in tests are fixed.
    if (typeof this.internals.setFormValue === 'function') {
      this.internals.setFormValue(this.bookingType.toString(), 'booking-calendar');
    }
    this.selectedBookingType = this.bookingType;
  }

  // Lifecycle methods
  connectedCallback() {
    debug('nylas-custom-booking-flow', 'connectedCallback');
  }

  disconnectedCallback() {
    debug('nylas-custom-booking-flow', 'disconnectedCallback');
  }

  componentWillLoad() {
    debug('nylas-custom-booking-flow', 'componentWillLoad');
    // See comment in the @Watch('name') decorator for more information.
    this.host.setAttribute('name', this.name);
  }

  componentDidLoad() {
    debug('nylas-custom-booking-flow', 'componentDidLoad');
  }

  componentWillUpdate() {
    debug('nylas-custom-booking-flow', 'componentWillUpdate');
  }

  componentDidUpdate() {
    debug('nylas-custom-booking-flow', 'componentDidUpdate');
  }

  componentWillRender() {
    debug('nylas-custom-booking-flow', 'componentWillRender');
  }

  componentDidRender() {
    debug('nylas-custom-booking-flow', 'componentDidRender');
  }

  @Listen('selectedOptionChanged')
  selectedOptionChangedHandler(event: CustomEvent<{ value: string; name: string }>) {
    debug('nylas-custom-booking-flow', 'selectedOptionChangedHandler', event.detail);
    // Pass as handler so that if event.defaultPrevented by parent app, this will be skipped.
    const valueChanged = (event: CustomEvent<{ value: string; name: string }>) => {
      const { value } = event.detail;
      this.selectedBookingType = value;
      this.internals.setFormValue(value.toString(), 'booking-calendar');
    };
    this.valueChanged.emit({ ...event.detail, valueChanged });
  }

  @RegisterComponent<NylasCustomBookingFlow, NylasSchedulerConfigConnector, Exclude<NylasSchedulerEditor['stores'], undefined>>({
    name: 'nylas-custom-booking-flow',
    stateToProps: new Map([['schedulerConfig.selectedConfiguration', 'selectedConfiguration']]),
    eventToProps: {
      valueChanged: async (
        event: CustomEvent<{ value: string; name: string; valueChanged?: (event: CustomEvent<{ value: string; name: string }>) => void }>,
        _nylasSchedulerConfigConnector: NylasSchedulerConfigConnector,
      ) => {
        const { valueChanged } = event.detail;
        // If a handler is passed, call it.
        if (valueChanged) {
          valueChanged(event);
        }
      },
    },
    fireRegisterEvent: true,
  })
  render() {
    const bookingOptions = [
      {
        label: 'Automatically accept bookings',
        value: 'booking',
      },
      {
        label: 'Manually confirm/decline bookings',
        value: 'pre-booking',
      },
    ];

    return (
      <Host>
        <div class="nylas-custom-booking-flow" part="ncbf">
          <div class="header" part="ncbf__header">
            <h3>Customize booking flow</h3>
            <p>Customize how new bookings are handled when a booking is completed.</p>
          </div>

          {bookingOptions.length > 0 ? (
            <div class="nylas-custom-booking-flow__dropdown">
              <span class="label">
                After a booking is confirmed
                <tooltip-component>
                  <info-icon slot="tooltip-icon" />
                  <span slot="tooltip-content">
                    Choose how youâ€™d like to handle new bookings. If you choose to manually confirm/decline bookings, you will be able to review and accept or decline each booking
                    request.
                  </span>
                </tooltip-component>
              </span>
              <select-dropdown
                name="booking-type"
                options={bookingOptions}
                withSearch={false}
                exportparts="sd_dropdown: ncbf__dropdown, sd_dropdown-button: ncbf__dropdown-button, sd_dropdown-content: ncbf__dropdown-content"
                defaultSelectedOption={bookingOptions.find(cal => cal.value == this.bookingType) ?? bookingOptions[0]}
              />
            </div>
          ) : (
            <div class="nylas-custom-booking-flow__loading">
              <span class="label">
                Select a calendar to book meetings on <info-icon />
              </span>
              <div class="loading-button">
                <span>Loading...</span>
              </div>
            </div>
          )}
        </div>
      </Host>
    );
  }
}
